<template>
    <v-card>
        <v-toolbar density="compact" color="primary">
            <v-btn icon @click="$emit('close')">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Todo追加</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
                <v-btn variant="text" @click="onSave">
                    <v-icon start icon="mdi-content-save-outline"></v-icon
                    >保存</v-btn
                >
            </v-toolbar-items>
        </v-toolbar>

        <v-container>
            <v-combobox
                v-model="category"
                :items="categories"
                density="compact"
                label="カテゴリ"
            ></v-combobox>
            <v-text-field
                v-model="title"
                label="タイトル"
                variant="outlined"
                clearable
            ></v-text-field>
            <v-text-field
                v-model="total"
                label="全量"
                variant="outlined"
                clearable
            ></v-text-field>
            <v-text-field
                v-model="progress"
                label="進捗"
                variant="outlined"
                clearable
            ></v-text-field>
        </v-container>
    </v-card>
</template>

<script>
export default {
    name: "NewTodo",
    props: {
        mode: Number,
        todo: Object,
    },
    components: {},
    data: () => ({
        category: null,
        categories: [
            "読書",
            "技術書",
            "学習",
            // "資格",
            "アプリ製作",
            "ゲーム",
            "プラモ",
            // "サウナ",
            // "バレー",
        ],
        title: "",
        total: 100,
        progress: 0,
    }),
    mounted() {
        if (this.mode == 1) {
            this.category = this.todo.category;
            this.title = this.todo.title;
            this.total = this.todo.total;
            this.progress = this.todo.progress;
        }
    },
    methods: {
        // onEmit() {
        //     this.$emit("close");
        //     this.$store.commit("increment");
        // },
        onSave() {
            if (this.mode == 0) {
                this.createTodo();
            } else {
                this.modTodo();
            }
            this.$emit("close");
        },
        async createTodo() {
            const d = new Date();
            const str = d.toLocaleString();

            const newTodo = {
                id: "",
                category: this.category,
                title: this.title,
                reg_date: str,
                update_date: str,
                total: this.total,
                progress: this.progress,
                progress_per: (this.progress / this.total) * 100,
            };

            newTodo.id = await this.createDigest();
            this.addTodo(newTodo);
            this.$store.dispatch("increment");
        },
        createDigest() {
            return new Promise((resolve) => {
                if (!crypto || !crypto.subtle) {
                    throw Error("crypto.subtle is not supported."); // ブラウザ未対応
                }
                var d = new Date();
                // console.log(d.toLocaleString());
                var str = d.toLocaleString();

                crypto.subtle
                    .digest("SHA-256", new TextEncoder().encode(str))
                    .then((x) => {
                        resolve(this.hexString(x)); // convert to hex string.
                    });
            });
        },
        hexString(buffer) {
            const byteArray = new Uint8Array(buffer);
            const hexCodes = [...byteArray].map((value) => {
                const hexCode = value.toString(16);
                const paddedHexCode = hexCode.padStart(2, "0");
                return paddedHexCode;
            });
            return hexCodes.join("");
        },
        modTodo() {
            const d = new Date();
            const str = d.toLocaleString();

            const todo = {
                id: this.todo.id,
                category: this.category,
                title: this.title,
                reg_date: this.todo.reg_date,
                update_date: str,
                total: this.total,
                progress: this.progress,
                progress_per: (this.progress / this.total) * 100,
            };

            this.updateTodo(todo);
            this.$store.dispatch("increment");
        },
    },
};
</script>
