<template>
    <v-data-table
        :headers="headers"
        :items="todoList"
        class="elevation-1"
        density="compact"
        item-key="id"
    >
        <template #[`item.progress_per`]="{ item }">
            <v-progress-linear
                v-model="item.columns.progress_per"
                color="success"
                height="25"
            >
                <template v-slot:default="{ value }">
                    <!-- <strong>{{ Math.ceil(value * 100) }}%</strong> -->
                    <strong>{{ Math.ceil(value) }}%</strong>
                </template>
            </v-progress-linear>
        </template>
        <template #[`item.edit`]="{ item }">
            <v-dialog v-model="new_todo_dialog[item.index]" width="80%">
                <template v-slot:activator="{ props }">
                    <v-btn variant="text" v-bind="props">
                        <v-icon icon="mdi-pencil-outline"></v-icon>
                    </v-btn>
                </template>
                <NewTodo
                    :mode="1"
                    :todo="item.raw"
                    @close="new_todo_dialog[item.index] = false"
                />
            </v-dialog>
        </template>
        <template #[`item.delete`]="{ item }">
            <v-btn variant="text">
                <v-icon icon="mdi-trash-can-outline"></v-icon>
                <v-overlay
                    activator="parent"
                    location="start center"
                    location-strategy="connected"
                    scroll-strategy="block"
                >
                    <v-btn @click="onDelete(item.raw)" color="warning"
                        >削除する</v-btn
                    >
                </v-overlay>
            </v-btn>
        </template>
    </v-data-table>
</template>

<script>
import NewTodo from "./NewTodo";

export default {
    name: "TodoList",
    components: { NewTodo },
    computed: {
        count() {
            return this.$store.getters.getCount;
        },
    },
    watch: {
        count() {
            console.log("refresh!!");
            this.todoList = this.getTodoList();
            this.initDialogFlg();
        },
    },
    data: () => ({
        todoList: [],
        headers: [
            {
                title: "カテゴリ",
                key: "category",
            },
            {
                title: "タイトル",
                key: "title",
            },
            {
                title: "進捗率",
                key: "progress_per",
            },
            {
                title: "編集",
                key: "edit",
            },
            {
                title: "削除",
                key: "delete",
            },
        ],
        new_todo_dialog: [],
    }),
    beforeMount() {
        this.todoList = this.getTodoList();
        this.initDialogFlg();
    },
    methods: {
        initDialogFlg() {
            this.new_todo_dialog = [...Array(this.todoList.length)].map(
                () => false
            );
        },
        filterPercent(n) {
            if (n == "-") {
                return "-";
            }
            return (n * 100).toFixed(1) + "%";
        },
        onDelete(todo) {
            this.deleteTodo(todo);
            this.$store.dispatch("increment");
        },
    },
};
</script>
